import { KetentuanPerusahaanComponent } from './ketentuan-perusahaan/ketentuan-perusahaan.component';
import { KetentuanPemerintahComponent } from './ketentuan-pemerintah/ketentuan-pemerintah.component';
import { CoreEditComponent } from './core-edit/core-edit.component';
import { CoreComponent } from './core/core.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ListComponent } from './ketentuan-perusahaan/list/list.component';
import { AddComponent } from './ketentuan-perusahaan/add/add.component';
import { EditComponent } from './ketentuan-perusahaan/edit/edit.component';
import { PegawaiComponent } from './pegawaii/pegawai/pegawai.component';



const routes: Routes = [
    {
        path: 'payroll',
        component: CoreComponent
    },
    {
        path: 'pegawai',
        component: PegawaiComponent
    },
    {
        path: 'payroll-edit/:id_pengaturan',
        component: CoreEditComponent
    },
    {
        path: 'ketentuan_pemerintah',
        component: KetentuanPemerintahComponent
    },
    {
        path: 'template-slip',
        loadChildren: () => import('./template-slip/template-slip.module').then(m => m.TemplateSlipModule)
    },
    {
        path: 'ketentuan_perusahaan',
        component: KetentuanPerusahaanComponent,
        children: [
            {
                path: '',
                redirectTo: 'list'
            },
            {
                path: 'list',
                component: ListComponent
            },
            {
                path: 'add',
                component: AddComponent
            },
            {
                path: 'edit/:id_komponen_perusahaan',
                component: EditComponent
            }
        ]
    },
    {
        path: 'perusahaan',
        loadChildren: () => import('./perusahaan/perusahaan.module').then(m => m.PerusahaanModule)
    },
    {
        path: 'utang',
        loadChildren: () => import('./utang/utang.module').then(m => m.UtangModule)
    },
    {
        path: 'tanda-tangan',
        loadChildren: () => import('./tanda-tangan/tanda-tangan.module').then(m => m.TandaTanganModule)
    },
    {
        path: 'komponen',
        loadChildren: () => import('./kelompok-komponen/kelompok-komponen.module').then(m => m.KelompokKomponenModule)
    },
    {
        path: 'aircraft',
        loadChildren: () => import('./aircraft/aircraft.module').then(m => m.AircraftModule)
    },
    {
        path: 'pekerjaan',
        loadChildren: () => import('./pekerjaan/pekerjaan.module').then(m => m.PekerjaanModule)
    },

    {
        path: 'ron',
        loadChildren: () => import('./ron/ron.module').then(m => m.RonModule)
    },
    {
        path: 'ron-group',
        loadChildren: () => import('./ron-group/ron-group.module').then(m => m.RonGroupModule)
    },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class PengaturanRoutingModule { }
