import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PengaturanComponent } from './pengaturan.component';


const routes: Routes = [
    {
        path: '',
        component: PengaturanComponent,
        children: [
            {
                path: 'status',
                loadChildren: () => import('./status/status.module').then(mo => mo.StatusModule)
            },
            {
                path: 'tipe-pegawai',
                loadChildren: () => import('./tipe-pegawai/tipe-pegawai.module').then(mo => mo.TipePegawaiModule)
            },
            {
                path: 'sp',
                loadChildren: () => import('./sp/sp.module').then(mo => mo.SpModule)
            },
            {
                path: 'jlisensi',
                loadChildren: () => import('./jlisensi/jlisensi.module').then(mo => mo.JlisensiModule)
            },
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class PengaturanRoutingModule { }
