import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { PayrollKomponenPerusahaanService, PayrollKelompokKomponenService, PayrollBpjsJenisService } from 'src/app/sdk/payroll/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Komponen_perusahaan, Kelompok_komponen, Bpjs_jenis } from 'src/app/sdk/payroll/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private activatedRoute: ActivatedRoute,
        private payrollKomponenPerusahaanService: PayrollKomponenPerusahaanService,
        private payrollKelompokKomponenService: PayrollKelompokKomponenService,
        private spinner: NgxSpinnerService,
        private payrollBpjsJenisService: PayrollBpjsJenisService,
        private router: Router
    ) { }


    ngOnInit() {
        this.activatedRoute.paramMap.subscribe(
            data => {
                if (data.get('id_komponen_perusahaan')) {
                    this.id_komponen_perusahaan = data.get('id_komponen_perusahaan');
                    this.form();
                    this.getPreeKomponen()
                    this.getKelompok()
                    this.getBpjs()
                }
            }
        )


    }
    bpjs: Bpjs_jenis[] = [];
    getBpjs() {
        this.payrollBpjsJenisService.getPayrollBpjsJenis().subscribe(
            data => {
                this.bpjs = data.data
            }
        )
    }
    list_kelompok: Kelompok_komponen[] = []
    getKelompok() {
        this.payrollKelompokKomponenService.getPayrollKelompokKomponens().subscribe(
            data => {
                this.list_kelompok = data.data
            }
        )
    }
    id_komponen_perusahaan: string = '';
    getPreeKomponen() {
        this.payrollKomponenPerusahaanService.getPayrollKomponenPerusahaansId(this.id_komponen_perusahaan).subscribe(
            data => {
                this.komponen.setValue(data.data);
                this.id_komponen_perusahaan = data.data.id_komponen_perusahaan;
            }
        )
    }

    komponen: FormGroupTypeSafe<Komponen_perusahaan>;
    form() {
        this.komponen = this.fb.group<Komponen_perusahaan>(
            {
                id_komponen_perusahaan: new FormControl('', [Validators.required]),
                id_perusahaan: new FormControl('', [Validators.required]),
                komponen: new FormControl('', [Validators.required]),
                rumus: new FormControl('', [Validators.required]),
                kode_komponen: new FormControl('', [Validators.required]),
                keterangan_komponen: new FormControl(''),
                metode: new FormControl('bulanan', [Validators.required]),
                prorate: new FormControl('ya', [Validators.required]),
                is_pajak: new FormControl('ya', [Validators.required]),
                is_tetap: new FormControl('ya', [Validators.required]),
                is_nilai: new FormControl('ya', [Validators.required]),
                is_lock: new FormControl('tidak', [Validators.required]),
                is_bpjs: new FormControl('tidak', [Validators.required]),
                jenis: new FormControl('plus', [Validators.required]),
                nilai: new FormControl('0'),
                validasi: new FormControl('1'),
                old_rumus: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                id_user: new FormControl('')
            }
        )
    }

    getValueForm(fild, compare) {
        if (this.komponen.get(fild).value == compare) {
            return true;
        }
        return false;
    }

    simpan() {
        this.spinner.show()
        if (this.komponen.invalid) {
            console.log(this.komponen.invalid)
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }
        this.komponen.patchValue({ validasi: 1 });

        this.payrollKomponenPerusahaanService.putPayrollKomponenPerusahaansId({
            id: this.id_komponen_perusahaan,
            body: this.komponen.value
        }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan, mohon cek Kode Komponen');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/payroll/module/pengaturan/ketentuan_perusahaan'])
    }


}
