import { Component, OnInit, Input } from '@angular/core';
import { PayrollDerazonaPegawaiDerazonaService } from 'src/app/sdk/payroll/services';
import { NzDrawerRef, NzMessageService } from 'ng-zorro-antd';
import { Router } from '@angular/router';
import { Pegawai_derazona } from 'src/app/sdk/payroll/models';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    @Input() id_pegawai = '';

    constructor(
        private drawerRef: NzDrawerRef<string>,
        private router: Router,
        private PayrollDerazonaPegawaiDerazonaService: PayrollDerazonaPegawaiDerazonaService,
        private nzMessageService: NzMessageService
    ) { }

    pegawai: Pegawai_derazona = {}


    id_pegawai_biodata: string = ''

    ngOnInit() {
        this.getPegawai()
    }

    getPegawai() {
        this.PayrollDerazonaPegawaiDerazonaService.getPayrollDerazonaPegawaiDerazonasId(this.id_pegawai).subscribe(data => {
            this.pegawai = data.data;

         })
    }
}
