import { Component, OnInit, Inject, Input } from '@angular/core';
import { id_ID, NzDrawerRef } from 'ng-zorro-antd';
import { PayrollDerazonaPegawaiDerazonaService, PayrollPtkpService } from 'src/app/sdk/payroll/services';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { Perusahaanp, Pegawai_derazona, Ptkp } from 'src/app/sdk/payroll/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-edit-pegawai',
    templateUrl: './edit-pegawai.component.html',
    styleUrls: ['./edit-pegawai.component.scss']
})
export class EditPegawaiComponent implements OnInit {

    @Input() id_pegawai = '';

    constructor(
        private drawerRef: NzDrawerRef<string>,
        private payrollDerazonaPegawaiDerazonaService: PayrollDerazonaPegawaiDerazonaService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private payrollPtkpService: PayrollPtkpService,
        private router: Router,
    ) { }


    ngOnInit() {
        this.form();
        this.getData();
    }
    listPTKP: Ptkp[] = []
    data: Pegawai_derazona = {}
    pegawai: Pegawai_derazona = {}
    getData() {
        this.payrollDerazonaPegawaiDerazonaService.getPayrollDerazonaPegawaiDerazonasId(this.id_pegawai).subscribe(
            data => {
                this.dataForm.setValue(data.data);

            }
        )

        this.payrollPtkpService.getPayrollPtkps().subscribe(
            data => {
                this.listPTKP = data.data
            }
        )
    }


    dataForm: FormGroupTypeSafe<Pegawai_derazona>;
    form() {
        this.dataForm = this.fb.group<Pegawai_derazona>(
            {
                nama_pegawai: new FormControl('', [Validators.required]),
                nik: new FormControl(''),
                tanggal_berhenti: new FormControl(''),
                tanggal_berkahir_jst: new FormControl(''),
                tanggal_lahir: new FormControl(''),
                tanggal_masuk: new FormControl(''),
                id_pegawai: new FormControl(''),
                id_ron_group: new FormControl(''),
                tanggal_masuk_jst: new FormControl(''),
                tanggal_milik_npwp: new FormControl(''),
                tanggal_terdaftar: new FormControl(''),
                telp: new FormControl(''),
                tempat_lahir: new FormControl(''),
                tinggi: new FormControl(''),
                total_jam: new FormControl(''),
                hash_absensi: new FormControl(''),
                hash_employee: new FormControl(''),
                hash_maintenance: new FormControl(''),
                hash_operation: new FormControl(''),
                hash_pilot: new FormControl(''),
                npwp: new FormControl(''),
                derazona_jam: new FormControl(''),
                nama_ibu_kandung: new FormControl(''),
                no_jamsostek: new FormControl(''),
                no_ktp: new FormControl(''),
                no_peserta_asuransi: new FormControl(''),
                alamat_pajak: new FormControl(''),
                alamat_tinggal: new FormControl(''),
                alasan_resign: new FormControl(''),
                email: new FormControl(''),
                kode_negara: new FormControl(''),
                pendidikan_terakhir: new FormControl(''),
                tanggal_penghasilan_mulai: new FormControl(''),
                penghasilan_sebelumnya: new FormControl(''),
                tanggal_penghasilan_selesai: new FormControl(''),
                pph_dipot_sebelumnya: new FormControl(''),
                agama: new FormControl(''),
                berat_badan: new FormControl(''),
                kebangsaan: new FormControl(''),
                keterangan: new FormControl(''),
                jabatan_pajak: new FormControl(''),
                jenis_kelamin: new FormControl(''),
                jenis_pegawai: new FormControl(''),
                jumlah_tanggungan: new FormControl(''),
                masa_penghasilan_sebelumnya: new FormControl(''),
                metode_pajak: new FormControl(''),
                status_berhenti: new FormControl(''),
                status_masuk: new FormControl(''),
                status_perkawinan: new FormControl(''),
                level_akses: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),

            }
        )
    }

    simpan() {
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            return;
        }

        if (this.dataForm.value.hash_absensi == 'tidak') {
            this.dataForm.patchValue({ hash_absensi: 'false' })
        } else {
            this.dataForm.patchValue({ hash_absensi: 'true' })
        }

        if (this.dataForm.value.hash_employee == 'tidak') {
            this.dataForm.patchValue({ hash_employee: 'false' })
        } else {
            this.dataForm.patchValue({ hash_employee: 'true' })
        }

        if (this.dataForm.value.hash_operation == 'tidak') {
            this.dataForm.patchValue({ hash_operation: 'false' })
        } else {
            this.dataForm.patchValue({ hash_operation: 'true' })
        }

        if (this.dataForm.value.hash_maintenance == 'tidak') {
            this.dataForm.patchValue({ hash_maintenance: 'false' })
        } else {
            this.dataForm.patchValue({ hash_maintenance: 'true' })
        }

        if (this.dataForm.value.hash_pilot == 'tidak') {
            this.dataForm.patchValue({ hash_pilot: 'false' })
        } else {
            this.dataForm.patchValue({ hash_pilot: 'true' })
        }

        this.data = this.dataForm.value;
        this.payrollDerazonaPegawaiDerazonaService.putPayrollDerazonaPegawaiDerazonasId({ id: this.id_pegawai, body: this.data }).subscribe(
            data => {
                this.close()
            }, err => {

                this.pesan.pesanWarningForm('Gagal di simpan');
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    close(): void {
        this.drawerRef.close();
    }



}
