import { Component, OnInit } from '@angular/core';
import { PayrollDerazonaPegawaiDerazonaService, PayrollDerazonaRonGroupService } from 'src/app/sdk/payroll/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzMessageService, NzDrawerService } from 'ng-zorro-antd';
import { FilterFuctionService } from 'src/app/services/filter-fuction.service';
import { Ron_group } from 'src/app/sdk/payroll/models';
import { EditPegawaiComponent } from '../edit-pegawai/edit-pegawai.component';
import { DetailComponent } from '../detail/detail.component';

@Component({
    selector: 'app-pegawai',
    templateUrl: './pegawai.component.html',
    styleUrls: ['./pegawai.component.scss']
})
export class PegawaiComponent implements OnInit {

    constructor(
        private payrollDerazonaPegawaiDerazonaService: PayrollDerazonaPegawaiDerazonaService,
        private spinner: NgxSpinnerService,
        private filterFuctionService: FilterFuctionService,
        private nzMessageService: NzMessageService,
        private drawerService: NzDrawerService,
        private payrollDerazonaRonGroupService: PayrollDerazonaRonGroupService

    ) { }


    ngOnInit() {
        this.getData()
    }

    listOfDisplayData: any[] = []
    listOfData: any[] = []
    ron_group: Ron_group[] = []
    getData() {

        this.payrollDerazonaRonGroupService.getPayrollDerazonaRonGroups().subscribe(
            data => {
                this.ron_group = data.data
            }
        )

        this.payrollDerazonaPegawaiDerazonaService.getPayrollDerazonaPegawaiDerazonas().subscribe(
            data => {
                this.listOfDisplayData = data.data;
                this.listOfData = data.data;


                data.data.forEach((element, index) => {
                    this.listOfData[index].hash_absensi = (element.hash_absensi == 'ya' ? true : false);
                    this.listOfData[index].hash_employee = (element.hash_employee == 'ya' ? true : false);
                    this.listOfData[index].hash_pilot = (element.hash_pilot == 'ya' ? true : false);
                    this.listOfData[index].hash_maintenance = (element.hash_maintenance == 'ya' ? true : false);
                    this.listOfData[index].hash_operation = (element.hash_operation == 'ya' ? true : false);
                });

            }
        )
    }

    searchValue = '';
    sortName: string | null = null;
    sortValue: string | null = null;
    reset(): void {
        this.searchValue = '';
        this.search();
        this.listOfDisplayData = this.listOfData
    }

    sort(sort: { key: string; value: string }): void {
        this.sortName = sort.key;
        this.sortValue = sort.value;
        this.search();
    }

    search(): void {
        const data = this.listOfData.filter(item => this.filterFuctionService.filterFunc(item, this.searchValue));
        console.log(data)
        this.listOfDisplayData = data.sort((a, b) =>
            this.sortValue === 'ascend'
                ? a[this.sortName!] > b[this.sortName!]
                    ? 1
                    : -1
                : b[this.sortName!] > a[this.sortName!]
                    ? 1
                    : -1
        );
    }

    update(data) {
        this.spinner.show();
        this.payrollDerazonaPegawaiDerazonaService.putPayrollDerazonaPegawaiDerazonasId({ id: data.id_pegawai, body: data }).subscribe(
            data => {
                this.spinner.hide()
            }
        )
    }

    editPegawai(id_pegawai): void {


        const drawerRef = this.drawerService.create<EditPegawaiComponent, { id_pegawai: string }, string>({
            nzTitle: 'Edit Pegawai',
            nzContent: EditPegawaiComponent,
            nzWidth: '700px',
            nzContentParams: {
                id_pegawai: id_pegawai
            }
        });

        drawerRef.afterOpen.subscribe(() => {
            console.log('Drawer(Component) open');
        });

        drawerRef.afterClose.subscribe(data => {
            this.getData()
            console.log(data);
            if (typeof data === 'string') {

            }
        });
    }

    detailPegawai(id_pegawai): void {


        const drawerRef = this.drawerService.create<DetailComponent, { id_pegawai: string }, string>({
            nzTitle: 'Edit Pegawai',
            nzContent: DetailComponent,
            nzWidth: '500px',
            nzContentParams: {
                id_pegawai: id_pegawai
            }
        });

        drawerRef.afterOpen.subscribe(() => {
            console.log('Drawer(Component) open');
        });

        drawerRef.afterClose.subscribe(data => {
            this.getData()
            console.log(data);
            if (typeof data === 'string') {

            }
        });
    }


}
