import { Komponen_perusahaan } from 'src/app/sdk/payroll/models/komponen-_perusahaan';
import { Component, OnInit } from '@angular/core';
import { PayrollKomponenPerusahaanService } from 'src/app/sdk/payroll/services/payroll-komponen-perusahaan.service';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { ActivatedRoute, Router } from '@angular/router';
import { NzNotificationService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private pesan: PesanService,
        private activatedRoute: ActivatedRoute,
        private payrollKomponenPerusahaanService: PayrollKomponenPerusahaanService,
        private nzNotificationService: NzNotificationService,
        private spinner: NgxSpinnerService,
        private router: Router,
    ) { }

    ngOnInit() {
        this.getList()
    }

    listKomponen: Komponen_perusahaan[] = []
    getList() {
        this.spinner.show()
        this.payrollKomponenPerusahaanService.getPayrollKomponenPerusahaans().subscribe(
            data => {
                this.listKomponen = data.data
                this.spinner.hide()
            }
        )
    }
    delete(id) {
        this.spinner.show()
        this.payrollKomponenPerusahaanService.deletePayrollKomponenPerusahaansId(id).subscribe(
            data => {
                this.getList()
                this.spinner.hide()
            }
        )
    }


}
