import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { PayrollPengaturanService, PayrollPkpService, PayrollPtkpService, PayrollBpjsJenisService } from 'src/app/sdk/payroll/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { Pengaturan, Ptkp, Pkp, Bpjs_jenis } from 'src/app/sdk/payroll/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-ketentuan-pemerintah',
    templateUrl: './ketentuan-pemerintah.component.html',
    styleUrls: ['./ketentuan-pemerintah.component.scss']
})
export class KetentuanPemerintahComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private payrollPengaturanService: PayrollPengaturanService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private payrollPkpService: PayrollPkpService,
        private payrollPtkpService: PayrollPtkpService
    ) { }

    id_pengaturan: string = ''
    selectedValueThrComponent: string = '';

    ngOnInit() {

        this.formPengaturan();

        this.getDetailPengaturan()
        this.getPkp()
        this.getPtkp()
    }

    pengaturan: FormGroupTypeSafe<Pengaturan>;
    getDetailPengaturan() {
        this.payrollPengaturanService.getPayrollPengaturans().subscribe(
            data => {
                this.pengaturan.setValue(data.data[0]);
                this.id_pengaturan = data.data[0].id_pengaturan
            }
        )
    }

    ptkp: Ptkp[] = [];
    pkp: Pkp[] = [];

    getPtkp() {
        this.payrollPtkpService.getPayrollPtkps().subscribe(
            data => {
                this.ptkp = data.data
            }
        )
    }
    getPkp() {
        this.payrollPkpService.getPayrollPkps().subscribe(
            data => {
                this.pkp = data.data
            }
        )
    }


    formPengaturan() {
        this.pengaturan = this.fb.group<Pengaturan>(
            {
                id_pengaturan: new FormControl('', [Validators.required]),
                id_perusahaan: new FormControl('', [Validators.required]),
                prorate_gaji: new FormControl('', [Validators.required]),
                prorate_gaji_metode: new FormControl('', [Validators.required]),
                cut_of_payroll: new FormControl('', [Validators.required]),
                cut_of_absensi: new FormControl('', [Validators.required]),
                tanggal_proses_payroll: new FormControl('', [Validators.required]),
                thr_start: new FormControl('', [Validators.required]),
                thr_pembulatan_masa_kerja: new FormControl('', [Validators.required]),
                thr_pembulatan_masa_kerja_ya: new FormControl('', [Validators.required]),
                thr_komponen: new FormControl('', [Validators.required]),
                thr_komponen_old: new FormControl('', [Validators.required]),
                biaya_jabatan: new FormControl('', [Validators.required]),
                biaya_jabatan_max: new FormControl('', [Validators.required]),
                created_at: new FormControl(''),
                template_payroll: new FormControl(''),
                template_thr: new FormControl(''),
                template_bonus: new FormControl(''),
                updated_at: new FormControl(''),
                id_user: new FormControl('')
            }
        )
    }

    getValueForm(fild, compare) {
        if (this.pengaturan.get(fild).value == compare) {
            return true;
        }
        return false;
    }

    simpan() {
        this.spinner.show()
        if (this.pengaturan.invalid) {
            console.log(this.pengaturan.invalid)
            this.pesan.pesanWarningForm('Form Biaya jabatan Tidak Valid');
            this.spinner.hide()
            return;
        }



        this.payrollPengaturanService.putPayrollPengaturansId({
            id: this.id_pengaturan,
            body: this.pengaturan.value
        }).subscribe(
            data => {

                this.payrollPkpService.putPayrollPkpsId({ id: '1', body: this.pkp }).subscribe(
                    respkp => {

                    }
                )

                this.payrollPtkpService.putPayrollPtkpsId({ id: '1', body: this.ptkp }).subscribe(
                    respkp => {

                    }
                )

                this.pesan.pesanBerhasilForm('Berhasil di simpan');
                // this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/payroll/module/pengaturan/pengaturan_pemerintah'])
    }

}
