import { Component, OnInit } from '@angular/core';
import { PayrollPengaturanService } from 'src/app/sdk/payroll/services/payroll-pengaturan.service';
import { Pengaturan } from 'src/app/sdk/payroll/models/pengaturan';
import { NzAlertModule, NzNotificationService } from 'ng-zorro-antd';

@Component({
    selector: 'app-core',
    templateUrl: './core.component.html',
    styleUrls: ['./core.component.scss']
})
export class CoreComponent implements OnInit {

    constructor(
        private payrollPengaturanService: PayrollPengaturanService,
        private nzNotificationService : NzNotificationService
    ) { }

    ngOnInit() {

        this.getPengaturan()
    }

    pengaturan: Pengaturan = {}
    getPengaturan() {
        this.payrollPengaturanService.getPayrollPengaturans().subscribe(
            res => {

                if (res.data.length < 1) {

                    this.nzNotificationService.create(
                        'warning',
                        'Pengaturan tidak ditemukan',
                        'Harap hubungi database administaror!'
                    );
                    return false;
                }
                this.pengaturan = res.data[0];
                this.pengaturan.thr_komponen = JSON.parse(this.pengaturan.thr_komponen)
            }
        )
    }

}
