import { NgxSpinnerService } from 'ngx-spinner';
import { Pengaturan } from 'src/app/sdk/payroll/models/pengaturan';
import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { PayrollPengaturanService, PayrollPrintTemplateService } from 'src/app/sdk/payroll/services';
import { NzNotificationService } from 'ng-zorro-antd';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { Validators, FormControl } from '@angular/forms';
import { Print_template } from 'src/app/sdk/payroll/models';

@Component({
    selector: 'app-core-edit',
    templateUrl: './core-edit.component.html',
    styleUrls: ['./core-edit.component.scss']
})
export class CoreEditComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private activatedRoute: ActivatedRoute,
        private payrollPengaturanService: PayrollPengaturanService,
        private nzNotificationService: NzNotificationService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private payrollPrintTemplateService: PayrollPrintTemplateService
    ) { }

    id_pengaturan: string = ''
    tgl: any[] = [];
    bulan: any[] = [];
    listThr_komponen: any[] = [];
    selectedValueThrComponent: string = '';

    ngOnInit() {

        this.activatedRoute.paramMap.subscribe(params => {
            this.id_pengaturan = params.get("id_pengaturan")
        })
        this.form();

        for (let i = 1; i <= 31; i++) {
            this.tgl.push(i);
        }

        for (let i = 1; i <= 12; i++) {
            this.bulan.push(i);
        }

        this.getDetailPengaturan()
        this.getTemplatePayslip('payroll');
        this.getTemplatePayslip('thr');
        this.getTemplatePayslip('bonus');
    }

    listTemplate: any = {}
    getTemplatePayslip(module) {
        this.payrollPrintTemplateService.getPayrollPrintTemplates(JSON.stringify({ module: module })).subscribe(
            data => {
                this.listTemplate[module] = data.data
            }
        )
    }

    pengaturan: FormGroupTypeSafe<Pengaturan>;
    getDetailPengaturan() {
        this.payrollPengaturanService.getPayrollPengaturansId(this.id_pengaturan).subscribe(
            data => {
                this.pengaturan.setValue(data.data);
            }
        )
    }

    form() {
        this.pengaturan = this.fb.group<Pengaturan>(
            {
                id_pengaturan: new FormControl('', [Validators.required]),
                id_perusahaan: new FormControl('', [Validators.required]),
                prorate_gaji: new FormControl('', [Validators.required]),
                prorate_gaji_metode: new FormControl('', [Validators.required]),
                cut_of_payroll: new FormControl('', [Validators.required]),
                cut_of_absensi: new FormControl('', [Validators.required]),
                tanggal_proses_payroll: new FormControl('', [Validators.required]),
                thr_start: new FormControl('', [Validators.required]),
                thr_pembulatan_masa_kerja: new FormControl('', [Validators.required]),
                thr_pembulatan_masa_kerja_ya: new FormControl('', [Validators.required]),
                thr_komponen: new FormControl('', [Validators.required]),
                thr_komponen_old: new FormControl('', [Validators.required]),
                biaya_jabatan: new FormControl('', [Validators.required]),
                biaya_jabatan_max: new FormControl('', [Validators.required]),
                template_payroll: new FormControl(''),
                template_thr: new FormControl(''),
                template_bonus: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                rate_usd: new FormControl(''),
                id_user: new FormControl('')
            }
        )
    }

    getValueForm(fild, compare) {
        if (this.pengaturan.get(fild).value == compare) {
            return true;
        }
        return false;
    }

    simpan() {
        this.spinner.show()
        if (this.pengaturan.invalid) {
            console.log(this.pengaturan.invalid)
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.payrollPengaturanService.putPayrollPengaturansId({
            id: this.id_pengaturan,
            body: this.pengaturan.value
        }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/payroll/module/pengaturan/payroll'])
    }

}
